body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



#root {
  height: 100%;
}
.App {
  text-align: center;
  height: 100%;
}

.layout {
  height: 100%;
}

.sider {
  background: white;
  padding: 5px;
  overflow:auto;
}

.content {
  padding: 15px;
  height: 100%;
}
.warpper {
    background: white;
    border-radius: 5px;
    padding: 2%;
    height: 100%;
    text-align: left;
    position: relative;
}

.nav-hader {
    height: 7%;
    display:flex;
    align-items:center;
    border-bottom: 1px solid #D3D3D3;
    margin-bottom: 2%;
    padding-left: 2%;
}

.window {
    height: 90%;
    width: 100%;
    padding: 15px;
    overflow: auto;
}
.file-card {
    border-radius: 5px;
    margin: 5px;
    text-align: left;
}

.treeNodeUnselectable {
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
}

.file-name {
    color: "#696969";
    font-size: 14;
    font-weight: 200;
}
.treeNodeUnselectable {
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
}

.file-name {
    color: "#696969";
    font-size: 14;
    font-weight: 200;
}

.file-card {
    margin: 3px;
    margin-top: 0px;
    margin-bottom: 0px;
}
.console-win {
    background: #FAFAFA;
    border: 1px solid rgb(192, 191, 191);
    margin: auto;
    width: 98%;
    flex: 1 1;
    text-align: left;
    /* overflow-y: auto; */
    overflow-y: hidden;
    z-index: 0;
}

#xterm-win {
    height: 100%;
}
#terminal-container {
    color: white;
    height: 100%;
}
